$(function() {
    window.launchCalculator = () => {
        if($('#tariff-calculator').length > 0) {
            new Vue({
                el: "#tariff-calculator",
                mounted() {
                    var _this = this;
                    var scale = document.getElementById('scale');

                    noUiSlider.create(scale, {
                        start: [1],
                        connect: 'lower',
                        range: {
                            'min': 1,
                            'max': 30
                        },
                        format: wNumb({
                            decimals: 0,
                            thousand: ' '
                        })
                    });

                    scale.noUiSlider.on('slide', function (values, handle) {
                        _this.userWatcher = false;
                        _this.usersChosen = values[handle];
                        setTimeout(() => {
                            _this.userWatcher = true
                        }, 50);

                    });
                },
                data() {
                    return {
                        fixedFee: parseInt($('#fixed_cost').val()),
                        perUser: parseInt($('#per_user_cost').val()),
                        accountingModuleCost: $('#accounting_module_cost').val(),
                        warehouseModuleCost: $('#warehouse_module_cost').val(),
                        currencySymbol: $('#currency_symbol').val(),
                        options: [
                            {
                                title: `  (${$('#accounting_module_cost').val()}${$('#currency_symbol').val()})`,
                                price: parseInt($('#accounting_module_cost').val()),
                                hint: "    , ,     "
                            },
                            {
                                title: `    (${$('#warehouse_module_cost').val()}${$('#currency_symbol').val()})`,
                                price: parseInt($('#warehouse_module_cost').val()),
                                hint: "    ,     ,     "
                            },
                            // { title: "     1 (34$)", price: 34, hint: "  24       " },
                            // { title: "     2 (50$)", price: 50, hint: "  24       " },
                            // { title: "  (20$)", price: 20, hint: "  24       " },
                        ],
                        selectedOptions: [],
                        usersChosen: 1,
                        userWatcher: true,
                        selectedPeriod: '',

                    }
                },
                computed: {
                    monthly() {
                        var options_value = 0;
                        this.selectedOptions.forEach(item => {
                            options_value += item.price
                        });
                        return Math.round((this.usersChosen * this.perUser) + this.fixedFee + options_value);
                    },
                    halfyear() {
                        return Math.round((this.monthly * 6) * 0.9);
                    },
                    year() {
                        return Math.round((this.monthly * 12) * 0.85);
                    },
                    selectedPrice() {
                        switch (this.selectedPeriod) {
                            case "":
                                return this.year
                            case "":
                                return this.halfyear
                            case "":
                                return this.monthly
                        }
                        return 0
                    },
                    formcomment() {
                        let str = ': ' + this.usersChosen
                        str += ', : ' + this.selectedPeriod
                        str += ',  : ' + (this.selectedOptions.length > 0 ? '' : '- ')
                        this.selectedOptions.forEach((el, index) => {
                            str += ((index > 0) ? ' | ' : '') + el.title
                        })
                        str += ',  : ' + this.selectedPrice + ' ' + this.currencySymbol
                        return str
                    }
                },
                methods: {
                    recordComment() {
                        $('#addcomment').val(this.formcomment)
                    },
                    buy(period) {
                        this.selectedPeriod = period
                        this.recordComment()
                    }
                },
                watch: {
                    usersChosen(val) {
                        if (this.userWatcher) {
                            var scaleInput = document.getElementById('scaleInput');
                            scale.noUiSlider.set([val, null]);
                            this.recordComment()
                        }
                    },
                    selectedOptions: {
                        deep: true,
                        handler() {
                            this.recordComment()
                        }
                    }
                }
            });
        }
    }
    window.launchCalculator()
});
