$(document).ready(function() {
    if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
    } else {
        if($(window).width() > 1200 && $('.scene').index() != -1) {
            var scene = document.getElementById('scene');
            new Parallax(scene, {
                relativeInput: true
            });
        }
    }

    var featuresLinks = $('.features__link');
    featuresLinks.on('click', function() {
        featuresLinks.removeClass('active');
        $(this).addClass('active');
        $('.features__tab').removeClass('active').eq($(this).index()).addClass('active');
    });

    $('.faq__question').on('click', function() {
        var block = $(this).parent();
        if(block.hasClass('open')) {
            block.removeClass('open').find('.faq__answer').slideUp();
        } else {
            block.addClass('open').find('.faq__answer').slideDown();
        }
    });

    $('.menu__btn').on('click', function() {
        if($(this).hasClass('open')) {
            $(this).removeClass('open');
            $('.nav').slideUp();
        } else {
            $(this).addClass('open');
            $('.nav').slideDown();
        }
    });

    var featuresSliders = $('.feature__slider');
    for(var i = 0; i < featuresSliders.length; i++) {
        (function () {
            var j = i;
            var featuresSlider = featuresSliders.eq(j).find('.feature__slides').owlCarousel({
                smartSpeed: 500,
                loop:true,
                items: 1,
                margin: 40,
                dots: false
            });

            featuresSliders.eq(j).find('.feature__slider-next').click(function() {
                featuresSlider.trigger('next.owl.carousel');
            });

            featuresSliders.eq(j).find('.feature__slider-prev').click(function() {
                featuresSlider.trigger('prev.owl.carousel');
            });
        })();
    }

    $('.features__tab').addClass('done');

    var reviewsSlider = $('.reviews__slides').owlCarousel({
        margin:30,
        autoplay: true,
        mouseDrag: false,
        autoplayTimeout: 4850,
        loop:true,
        items: 3,
        responsive:{
            0:{
                margin: 20,
                items: 1
            },
            500:{
                margin: 20,
                items: 1
            },
            1200:{
                items: 3
            }
        }
    });

    $('.reviews__control-next').click(function() {
        reviewsSlider.trigger('next.owl.carousel');
        $('.reviews__dot').addClass('stop');
        reviewsSlider.data('owl.carousel').options.autoplay = false;
        reviewsSlider.trigger( 'refresh.owl.carousel' );
    });

    $('.reviews__control-prev').click(function() {
        reviewsSlider.trigger('prev.owl.carousel');
        $('.reviews__dot').addClass('stop');
        reviewsSlider.data('owl.carousel').options.autoplay = false;
        reviewsSlider.trigger( 'refresh.owl.carousel' );
    });

    reviewsSlider.on('translated.owl.carousel', function (e) {
        $('.reviews__dot').removeClass('active');
        $('.reviews__dot' + '[data-index= ' + $('.reviews__slider .owl-item.active').find('.reviews__slide').data('index') + ']').addClass('active');
    });

    $('.reviews__dot').on('click', function() {
        $('.reviews__dot').removeClass('active');
        $(this).addClass('active');
        $('.reviews__dot').addClass('stop');
        reviewsSlider.data('owl.carousel').options.autoplay = false;
        reviewsSlider.trigger( 'refresh.owl.carousel' );
        reviewsSlider.trigger('to.owl.carousel', $(this).data('index'));
    });

    $('.logos__all').on('click', function() {
        $('.logos__list').addClass('open');
        $(this).css('display', 'none');
    });

    $('.text-block__all').on('click', function() {
        $('.text-block__text').addClass('open');
        $(this).css('display', 'none');
    });

    $('.review__text-else').on('click', function() {
        $(this).parent().addClass('open');
        $(this).css('display', 'none');
    });

    $(document).on('click', '.prices', function() {
        $(this).toggleClass('open');
    });

    $(document).on('click', '.js-popup-link', function(e) {
        // $('.js-popup-link').on('click', function (e) {
        e.preventDefault();
        var self = this;

        if($(this).data('title')) {
            $('#popup-title').text($(this).data('title'));
        }

        if($(this).data('type')) {
            $('#bittype').val($(this).data('type'));
        }

        if($(this).data('btn')) {
            $('#popup-btn').text($(this).data('btn'));
        }

        $('html').height($(window).height()).css('overflow', 'hidden');
        $('.page-wrap').css('overflow', 'scroll');

        if($(this).data('video')) {
            $('.popup-video .popup-video__wrap').append('<iframe src="https://www.youtube.com/embed/' + $(this).data('video') + '?rel=0&autoplay=1" frameborder="0" allowfullscreen></iframe>');
        }

        if($(this).data('form')) {
            $('.overlay').addClass('form');
            if($(this).data('comment')) {
                $('#addcomment').val($(this).data('comment'));
            }
        } else {
            $('.overlay').removeClass('form');
        }

        $('.overlay').fadeIn(300,
            function () {
                $($(self).data('href'))
                    .css('display', 'block')
                    .stop().animate({opacity: 1}, 200);
                // window.launchForms();
                // setTimeout(() => {
                //     window.launchForms();
                // },50);


            });
        return false;
    });

    window.popupClose = () => {
        $('.popup').stop().animate({opacity: 0}, 300,
            function () {
                $('.popup-video iframe').detach();
                $('.page-wrap').css('overflow', 'hidden');
                $('html').css('overflow', 'auto');
                $(this).css('display', 'none');
                $('.overlay').stop().fadeOut(400);
            }
        );
        $('#bittype').val("");
    }

    $('.popup').on('click', function(e){
        e.stopPropagation();
    });

    $('.overlay__close, .overlay, .popup__close').on('click', window.popupClose);



    if($(window).width() <= 750) {
        $(document).on('click', '.tariffs__tab', function() {
        // $('.tariffs__tab').on('click', function() {
            $('.tariffs__tab.active').removeClass('active');
            $(this).addClass('active');
            $('.tariffs__table td, .tariffs__control').css('display', 'none');
            $('.tariffs__control').eq($(this).index()).css('display', 'block');
            $('.tariffs__table td:first-child').css('display', 'table-cell');
            $('.tariffs__table td:nth-child(' + ($(this).index() + 2) + ')').css('display', 'table-cell');
        });
        $('.tariffs__tab[data-index="1"]').trigger('click');
    }

});

$(function() {
    $(document).on('click', '[data-toggle="tab"]', function() {
        let id = $(this).data('target');
        $('.tab-content, [data-toggle="tab"]').removeClass('active')
        $(id).addClass('active')
        $(this).addClass('active')
    })

    $(document).on('click', '.choose-currency', function() {
        let currency = $(this).data('currency');
        let text = $(this).data('title');
        $.post('/currency', {
            _token: document.head.querySelector('meta[name="csrf-token"]').content,
            currency: currency,
            view: $(this).data('view')
        }).done((res) => {
            $('#dynamic-tariffs').html(res);
            $('.tariffs__select-selected').text(text);
            window.launchCalculator()
            $('.tariffs__tab[data-index="1"]').trigger('click');
            // location.reload()
        })
    })

    $(document).on('click', 'a.new_tariff_button', function (event) {
        var val = '.show_price_for_'+$(this).data('val');
        $('a.new_tariff_button').removeClass('active');
        $(this).addClass('active');
        $('.show_price_for').hide();
        $(val).show();
    })

    $(document).on('click', '.action__close', function() {
        $('section.action').remove();
        $.post('/banner/close', {
            _token: document.head.querySelector('meta[name="csrf-token"]').content,
        })
    });

    if($('#superoffer_date').length > 0) {
        var countDownDate = new Date($('#superoffer_date').data('value').replace(' ', 'T')).getTime();
        setInterval(function() {
            var now = new Date().getTime();
            var timeleft = countDownDate - now;
            var days = Math.floor(timeleft / (1000 * 60 * 60 * 24));
            var hours = Math.floor((timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            var minutes = Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60));
            var seconds = Math.floor((timeleft % (1000 * 60)) / 1000);
            $('#days-left').text(zeroPad(days));
            $('#hours-left').text(zeroPad(hours));
            $('#minutes-left').text(zeroPad(minutes));
            $('#seconds-left').text(zeroPad(seconds));
        }, 1000);
    }

    new WOW().init();

    $('.feature__slide img, .help__content img, .article img').click(function() {
        $.fancybox.open('<img src="'+$(this).attr('src')+'"/>');
    });

});

function zeroPad(string) {
    return ('0' + string).slice(-2)
}

function mapsInit(){
    var map = new ymaps.Map("map",
        {
            center: [43.24975907453032,76.95743049999994],
            zoom: 13,
            controls: []

        })
        ,placemark = new ymaps.Placemark([43.24975907453032,76.95743049999994], {
        hintContent: '. , .  59'
    }, {
        iconLayout: 'default#image',
        iconImageHref: 'images/mark.png',
        iconImageSize: [55, 78],
        iconImageOffset: [-27, -78]
    });

    map.behaviors.disable('scrollZoom');
    map.geoObjects.add(placemark);
}


if($('#map').index() != -1) {
    ymaps.ready(mapsInit);
}

import './form'
import './calculator'
